import React, { useEffect } from 'react';
import styled, { css } from 'styled-components';
import T from 'prop-types';
import { ThugPugsThemeProvider } from '@theme/provider';
import { themeVal } from '@theme/utils';

import SEO from './seo';
import PageHeader from './page-header';
import PageFooter from './page-footer';

const renderPageVariation = ({ variation }) => {
  switch (variation) {
    case 'base':
      return css`
        background: ${themeVal('color.base')};
      `;
  }
};

const Page = styled.div`
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  ${renderPageVariation}
`;

const PageBody = styled.div`
  flex-grow: 1;
  display: flex;
  flex-direction: column;
`;

const Layout = ({ children, pageVariation, title, footerType }) => {
  useEffect(() => {
    document.documentElement.style.setProperty(
      '--scrollbar-width',
      window.innerWidth - document.documentElement.clientWidth + 'px'
    );
  }, []);

  return (
    <ThugPugsThemeProvider>
      {/* eslint-disable-next-line */}
      <SEO title={title || 'Welcome'} />
      <Page variation={pageVariation}>
        <PageHeader />
        <PageBody>{children}</PageBody>
        <PageFooter type={footerType} />
      </Page>
    </ThugPugsThemeProvider>
  );
};

export default Layout;

Layout.propTypes = {
  footerType: T.oneOf(['credits']),
  pageVariation: T.string,
  title: T.string,
  children: T.oneOfType([
    T.node,
    T.arrayOf(T.oneOfType([T.node, T.arrayOf(T.node)]))
  ])
};
