import React, { useEffect, useState } from 'react';
import styled, { css } from 'styled-components';
import { Link } from 'gatsby';

import { glsp, themeVal } from '@theme/utils';
import { visuallyHidden, listReset } from '@theme/helpers';
import { media } from '@theme/media';
import Constrainer from '../styles/constrainer';
import { IconDiscord } from './icons/discord';
import { IconOpenSea } from './icons/opensea';
import { IconTwitter } from './icons/twitter';
import { IconHamburgerMenu } from './icons/hamburger-menu';
import { Button } from '../styles/button';
import { IconXmarkSmall } from './icons/xmark--small';
import UnscrollableBody from './unscrollable-body';

import logoUrl from '../images/thug-pugs-logo.svg';
import { useMediaQuery } from '../utils/use-media-query';
import { Heading } from '../styles/heading';

const PageHeaderSelf = styled.header`
  position: relative;
  z-index: 10;
`;

const PageHeaderInner = styled(Constrainer)`
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  padding-top: ${glsp()};
  padding-bottom: ${glsp()};
  gap: ${glsp(1.5)};

  ${media.smallUp`
    gap: ${glsp(3)};
  `}
`;

const Brand = styled.div`
  a {
    display: grid;
    align-items: center;

    &,
    &:visited {
      color: inherit;
      text-decoration: none;
    }
  }
`;

const SiteNav = styled.nav`
  display: flex;
  flex: 1;
`;

const SiteMenu = styled.ul`
  ${listReset()}
  display: flex;
  flex: 1;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  gap: ${glsp(3)};

  li {
    display: flex;
    align-items: center;
  }
`;

const SiteMenuLink = styled(Link)`
  text-decoration: none;
  color: ${themeVal('color.surface')};
  font-weight: ${themeVal('type.base.medium')};
  display: flex;
`;

const GlobalMenuWrapper = styled.div`
  position: fixed;
  inset: 0 0 0 auto;
  z-index: 900;
  display: flex;
  flex-flow: column;
  gap: ${glsp()};
  width: 15rem;
  margin-right: -15rem;
  transition: margin 0.24s ease 0s;
  background: ${themeVal('color.base')};
  padding: ${glsp()};

  ${({ revealed }) =>
    revealed &&
    css`
      & {
        margin-right: 0;
      }
    `}

  &::after {
    content: '';
    position: absolute;
    inset: 0 0 0 auto;
    z-index: -1;
    background: transparent;
    width: 0;
    transition: background 0.64s ease 0s;

    ${({ revealed }) =>
      revealed &&
      css`
        & {
          ${media.smallDown`
            background: ${themeVal('color.base-400a')};
            width: 200vw;
        `}
        }
      `}
  }

  ${media.mediumUp`
    position: static;
    background: none;
    margin-right: 0;
    padding: 0;
    flex: 1;
  `}
`;

const GlobalMenuHeadline = styled.div`
  display: flex;
  align-items: center;

  ${media.mediumUp`
    display: none;
  `}
`;

const GlobalMenu = styled(SiteMenu)`
  gap: ${glsp(0.5)};
  flex-flow: column nowrap;
  align-items: start;

  li {
    width: 100%;
  }

  ${media.mediumUp`
    flex-flow: row;
    gap: ${glsp(2)};

    li {
      width: auto;
    }

    li:first-child {
      display: none;
    }
  `}
`;

const GlobalMenuLink = styled(SiteMenuLink).attrs({
  activeClassName: 'active'
})`
  position: relative;
  flex: 1;
  margin: ${glsp(0, -1)};
  padding: ${glsp(0.25, 1)};
  transition: opacity 160ms ease-in-out, background 160ms ease-in-out;

  &:hover {
    background: ${themeVal('color.surface-200a')};
  }

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0.125rem;
    height: 0;
    background: currentColor;

    ${media.mediumUp`
      width: 0;
      height: 0.125rem;
      bottom: -0.125rem;
    `}
  }

  &.active::after {
    ${media.smallDown`
      height: 100%;
    `}

    ${media.mediumUp`
      width: 100%;
    `}
  }

  ${media.mediumUp`
    margin: 0;
    padding: 0;

    &:hover {
      background: none;
      opacity: 0.64;
    }
  `}
`;

const SocialMenu = styled(SiteMenu)`
  gap: ${glsp(2)};
  flex: 0;

  ${media.mediumUp`
    margin-left: auto;
  `}
`;

const SocialMenuLink = styled(SiteMenuLink).attrs({ as: 'a' })`
  transition: opacity 160ms ease-in-out;

  &:hover {
    opacity: 0.64;
  }

  span {
    ${visuallyHidden()}
  }
`;

const MenuOpen = styled(Button)`
  margin-left: auto;

  ${media.mediumUp`
    display: none;
  `}
`;

const MenuClose = styled(Button)`
  margin-left: auto;
`;

function PageHeader() {
  const { isSmallDown } = useMediaQuery();

  const [globalNavRevealed, setGlobalNavRevealed] = useState(!isSmallDown);

  // Close global nav when media query changes.
  useEffect(() => {
    setGlobalNavRevealed(!isSmallDown);
  }, [isSmallDown]);

  return (
    <PageHeaderSelf>
      {globalNavRevealed && isSmallDown && <UnscrollableBody />}
      <PageHeaderInner>
        <Brand>
          <Link to='/' aria-label='Thug Pugs'>
            <img
              src={logoUrl}
              alt='Thug Pugs logotype'
              width={84}
              height={84}
            />
          </Link>
        </Brand>
        <SiteNav>
          <GlobalMenuWrapper revealed={globalNavRevealed}>
            {isSmallDown && (
              <GlobalMenuHeadline>
                <Heading size='xsmall' variation='surface' as='p'>
                  Menu
                </Heading>
                <MenuClose
                  variation='surface-text'
                  fitting='skinny'
                  onClick={() => setGlobalNavRevealed(false)}
                >
                  <IconXmarkSmall title='Toggle icon for the navigation menu' />
                </MenuClose>
              </GlobalMenuHeadline>
            )}
            <GlobalMenu>
              <li>
                <GlobalMenuLink
                  to='/'
                  onClick={() => setGlobalNavRevealed(false)}
                >
                  Welcome
                </GlobalMenuLink>
              </li>
              <li>
                <GlobalMenuLink
                  to='/about'
                  onClick={() => setGlobalNavRevealed(false)}
                >
                  About
                </GlobalMenuLink>
              </li>
              <li>
                <GlobalMenuLink as='a' href='https://www.thugpugs.com/tbone'>
                  $TBONES
                </GlobalMenuLink>
              </li>
              <li>
                <GlobalMenuLink as='a' href='https://www.thugpugs.com/names'>
                  Names
                </GlobalMenuLink>
              </li>
              <li>
                <GlobalMenuLink
                  key='domains'
                  as='a'
                  href='https://www.thugpugs.com/domains'
                >
                  Domains
                </GlobalMenuLink>
              </li>
            </GlobalMenu>
          </GlobalMenuWrapper>

          <SocialMenu>
            <li>
              <SocialMenuLink href='https://opensea.io/collection/thugpugs'>
                <IconOpenSea />
                <span>OpenSea</span>
              </SocialMenuLink>
            </li>
            <li>
              <SocialMenuLink href='https://discord.gg/thugpugs'>
                <IconDiscord />
                <span>Discord</span>
              </SocialMenuLink>
            </li>
            <li>
              <SocialMenuLink href='https://twitter.com/thugpugsnft'>
                <IconTwitter />
                <span>Twitter</span>
              </SocialMenuLink>
            </li>
          </SocialMenu>
        </SiteNav>
        {isSmallDown && (
          <MenuOpen
            variation='surface-text'
            fitting='skinny'
            onClick={() => setGlobalNavRevealed(true)}
          >
            <IconHamburgerMenu title='Toggle icon for the navigation menu' />
          </MenuOpen>
        )}
      </PageHeaderInner>
    </PageHeaderSelf>
  );
}

export default PageHeader;
